import React from 'react';
import { cn } from '../../lib/utils';

interface BadgeProps extends React.HTMLAttributes<HTMLDivElement> {
  variant?: 'default' | 'secondary' | 'destructive' | 'outline' | 'success' | 'warning';
}

const Badge = React.forwardRef<HTMLDivElement, BadgeProps>(
  ({ className, variant = 'default', ...props }, ref) => {
    const variants = {
      default:
        "border-transparent bg-primary text-primary-foreground hover:bg-primary/80 bg-cyan-600 text-white",
      secondary:
        "border-transparent bg-secondary text-secondary-foreground hover:bg-secondary/80 bg-gray-700 text-gray-100",
      destructive:
        "border-transparent bg-destructive text-destructive-foreground hover:bg-destructive/80 bg-red-600 text-white",
      outline: "text-foreground border-gray-600 text-gray-100",
      success:
        "border-transparent bg-green-600 text-white hover:bg-green-700",
      warning:
        "border-transparent bg-yellow-600 text-white hover:bg-yellow-700",
    };

    return (
      <div
        ref={ref}
        className={cn(
          "inline-flex items-center rounded-full border px-2.5 py-0.5 text-xs font-semibold transition-colors focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2",
          variants[variant],
          className
        )}
        {...props}
      />
    );
  }
);
Badge.displayName = "Badge";

export { Badge, type BadgeProps };